export const WORDLE = 'wordle';
const WORDLE_LINK = 'hwahae://webview?url=https://gyuusik.github.io/';
const GAMES = [WORDLE];
export type Game = (typeof GAMES)[number];

const useGameLink = (kind: Game): string => {
  switch (kind) {
    case WORDLE:
    default:
      return WORDLE_LINK;
  }
};

export default useGameLink;
