import { css } from '@emotion/react';

import {
  AnimatedInView,
  DEFAULT_OPTIONS,
  LEFT_RIGHT_OPTIONS,
} from 'src/components/animated';
import { Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import type { NormalPaperItem } from 'src/hooks/usePaperItems';

import Badge from './Badge';
import PointingContent from './PointingContent';
import { getContainerStyle } from './styles';
import Typographies from './Typographies';

const paperLayout = css`
  display: flex;
  color: #ffffff;
`;

interface NormalPaperProps extends NormalPaperItem {}

const NormalPaper = ({
  imageUrl,
  paperType,
  staticContent,
  linkableContents,
}: NormalPaperProps): JSX.Element => {
  const { contentLayout, titleStyle, descriptionStyle } =
    getContainerStyle(paperType);

  return (
    <Paper backgroundUrl={imageUrl} css={paperLayout}>
      <Stack css={contentLayout} spacing={12}>
        {staticContent?.badge && (
          <AnimatedInView options={LEFT_RIGHT_OPTIONS}>
            <Badge
              variant={staticContent.badge.variant}
              hasBorder={staticContent.badge.hasBorder}
            >
              {staticContent.badge.content}
            </Badge>
          </AnimatedInView>
        )}
        <AnimatedInView>
          <Typographies
            name="title"
            contentAndVariant={staticContent.title}
            isUpperCase
            css={titleStyle}
          />
        </AnimatedInView>

        <AnimatedInView
          options={{
            ...DEFAULT_OPTIONS,
            config: {
              ...DEFAULT_OPTIONS.config,
            },
          }}
        >
          <Typographies
            name="description"
            contentAndVariant={staticContent?.description}
            css={descriptionStyle}
          />
        </AnimatedInView>
      </Stack>

      {linkableContents?.map((content, index) => {
        const key = `linker-${index}`;
        return <PointingContent key={key} {...content} />;
      })}
    </Paper>
  );
};

export default NormalPaper;
