import { css } from '@emotion/react';

import { FullScreenBox } from 'src/components/layout';
import { IntroPaper, NormalPaper, StampPaper } from 'src/components/papers';
import type { NormalPaperItem, PaperItem } from 'src/hooks/usePaperItems';
import usePaperItems, { INTRO } from 'src/hooks/usePaperItems';
import useProductInfo from 'src/hooks/useProductInfo';

const isNormalCard = (item: PaperItem): item is NormalPaperItem => {
  return item.paperType !== INTRO;
};

// scroll-snap-points-y: repeat(100vh);
const scrollSnapTypeMandatory = css`
  overflow: scroll;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
`;

const Papers = (): JSX.Element => {
  const { name: productName } = useProductInfo();
  const items = usePaperItems({ productName });
  const introItem = items.find(({ paperType }) => paperType === INTRO);
  const normalItems = items.filter(isNormalCard);

  return (
    <FullScreenBox css={scrollSnapTypeMandatory}>
      {introItem && <IntroPaper imageUrl={introItem?.imageUrl} />}
      {normalItems.map(
        ({ paperType, imageUrl, staticContent, linkableContents }, index) => {
          const key = `${index}-${paperType}`;
          return (
            <NormalPaper
              key={key}
              imageUrl={imageUrl}
              paperType={paperType}
              staticContent={staticContent}
              linkableContents={linkableContents}
            />
          );
        },
      )}
      <StampPaper />
    </FullScreenBox>
  );
};

export default Papers;
