import type { UseSpringProps } from '@react-spring/web';
import { animated, easings, useInView } from '@react-spring/web';

export const STEP_UNIT = 2000;
export const DEFAULT_OPTIONS: UseSpringProps = {
  from: {
    opacity: 0,
    y: 100,
  },
  to: {
    opacity: 1,
    y: 0,
  },
  config: {
    easing: easings.easeInOutBack,
  },
};

export const LEFT_RIGHT_OPTIONS: UseSpringProps = {
  from: {
    opacity: 0,
    x: -100,
  },
  to: {
    opacity: 1,
    x: 0,
  },
  config: {
    easing: easings.easeInOutBack,
  },
};

export const ELASTIC_OPTIONS: UseSpringProps = {
  from: {
    opacity: 0.5,
    x: -100,
  },
  to: {
    opacity: 1,
    x: 0,
  },
  config: {
    easing: easings.easeOutElastic,
  },
};

interface AnimatedInViewProps {
  children: React.ReactNode;
  options?: UseSpringProps;
}
const AnimatedInView = ({
  children,
  options = DEFAULT_OPTIONS,
  ...others
}: AnimatedInViewProps): JSX.Element => {
  const [ref, springs] = useInView(() => options);
  return (
    <animated.div ref={ref} style={springs} {...others}>
      {children}
    </animated.div>
  );
};

export default AnimatedInView;
